.selectDiv {
    border: 1px solid #ccc; /* 회색 외곽선 */
    max-width: 480px; /* 최대 너비 설정 */
    padding: 10px; /* 내용과 외곽선 사이 간격 */
}

.playerTable {
    border: 1px solid #ccc; /* 회색 외곽선 */
    width: 500px; /* 최대 너비 설정 */
}

/* 테이블 셀(td)에 외곽선 추가 */
.playerTable td {
    border: 1px solid #ccc; /* 회색 외곽선 */
    padding: 8px; /* 셀 내용과 외곽선 사이 간격 */
}